import './App.css';
import ErrorBoundary from './errorBoundary';
import { PageContextProvider } from './components/PageContext/PageContext';

function App(props) {

  return (
    <ErrorBoundary>
      <PageContextProvider value={props}>
        <section data-app-prefix={props.app_prefix} className="app">
          <h1 className="text">Z-APP</h1>
          <p className="manifest">
            id: {props.id} <br/>
            name: {props.name} <br/>
            app_prefix: {props.app_prefix} <br/>
            description: {props.description} <br/>
            version: {props.version} <br/>
            team: {props.team} <br/>
            entry_script: {props.entry_script} <br/>
            iframe: {props.entry_iframe} <br/>
            email_contacts: {props.email_contacts} <br/>
            environment: {props.environment} <br/>
          </p>
        </section>
      </PageContextProvider>
    </ErrorBoundary>
  );
}

export default App;